import React from "react";
import FadeContent from "../../components/FadeContent/FadeContent";
import AnimatedList from "../../components/AnimatedList/AnimatedList";
import ScrollStack, { ScrollStackItem } from "../../components/ScrollStack/ScrollStack";
import AboutContent from "../../content/AboutContent.json";
import { AboutContainer, AboutTextContainer, BulletsContainer, BulletItem } from "./styles";

interface AboutUsContentProps {
  titleComponent?: React.ReactNode;
}

const AboutUsContent = ({ titleComponent }: AboutUsContentProps) => {
  return (
    <AboutContainer>
      {/* TÃ­tulo animado personalizado */}
      {titleComponent && <div style={{ textAlign: "center", marginBottom: "1.5rem" }}>{titleComponent}</div>}

      {/* Texto introductorio con fade */}
      <FadeContent blur={true} duration={1000} easing="ease-out" initialOpacity={0}>
        <AboutTextContainer>
          <p>{AboutContent.text}</p>
        </AboutTextContainer>
      </FadeContent>

      {/* Lista de bullets - PRUEBA CON SCROLLSTACK */}
      <BulletsContainer>
        <ScrollStack
          className="about-scroll-stack"
          itemDistance={120}
          itemScale={0.05}
          itemStackDistance={40}
          stackPosition="30%"
          scaleEndPosition="15%"
          baseScale={0.85}
          scaleDuration={0.5}
          rotationAmount={2}
          blurAmount={0}
          useWindowScroll={false}
          onStackComplete={() => console.log("Stack animation complete")}>
          {AboutContent.bullets.map((bullet, index) => (
            <ScrollStackItem key={index} itemClassName="bullet-card">
              <p>{bullet}</p>
            </ScrollStackItem>
          ))}
        </ScrollStack>
      </BulletsContainer>

      {/* 
      VERSIÃ“N BACKUP CON ANIMATEDLIST (100% FUNCIONAL)
      <BulletsContainer>
        <AnimatedList
          items={AboutContent.bullets}
          showGradients={false}
          enableArrowNavigation={false}
          displayScrollbar={false}
          className="about-bullets-list"
          itemClassName="bullet-item"
          onItemSelect={(item: string, index: number) => {
            console.log(`Selected bullet ${index}: ${item}`);
          }}
        />
      </BulletsContainer>
      */}
    </AboutContainer>
  );
};

export default AboutUsContent;
